header {
  background-color: #919ab4;
  height: 90px;
  max-width: 70vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 20px;
  font-family: Impact;
  color: #000;
  /* centers the div */
  margin: 0 auto;
  padding: 0 2rem;
  border-color: 0 solid #000;
  border-width: 0 0.4rem 0.4rem 0.4rem;
  border-style: solid;
  border-radius: 0 0 1rem 1rem;
  line-height:2rem;
}

.Header-logo-left {
  height: 100%;
}
.Header-logo-right {
  height: 100%;
}

@media only screen and (max-width:700px){
  .Header-logo-right {
    display: none;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .Header-logo-left {
    animation: Header-logo-clockwise 1s;
  }
  .Header-logo-right {
    animation: Header-logo-counterclockwise 1s;
  }
}

@keyframes Header-logo-clockwise {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Header-logo-counterclockwise {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}