.ControlPanel {
  background-color: #919ab4;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 3rem;
  min-width: 55vw;
  max-width: 65vw;
  margin: 0 auto;
  border-color: 0 solid #000;
  border-width: 0 0.4rem 0 0.4rem;
  border-style: solid;
}
.ControlPanel select {
  background-color: #878da0;
  color: #000;
  min-width:15%;
  border: 2px solid #000;
  border-radius: 0.5rem;
  font-size: 15px;
  font-family: Impact;
  text-align: center;
}

.ControlPanel input {
  background-color: #878da0;
  color: #000;
  font-size: 18px;
  font-family: Impact;
  border: 2px solid #000;
  border-radius: 0.5rem;
}
.ControlPanel input:hover {
  background-color: #000;
  color: #919ab4;
}
@media only screen and (max-width:690px) {
  .ControlPanel {
  }
}