.App { 
  background-color: #666e8b;
}

.App {
  text-align: center;
  font-family: Impact;
  height:100vh;
}

.App-body {
  background-color: #919ab4;
  height: 70vh;
  max-width: 70vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: min(20px, 3vw);
  color: #000;
  /* centers the div */
  margin: 0 auto;
  padding: 0 2rem;
  border-color: 0.4rem solid #000;
  border-width: 0.4rem;
  border-style: solid;
  border-radius: 1rem;
}